<template>
  <div class="CommintyCard-detail">
    <template v-if="CommintyCardDetailData.type==`pictureFollowCard`">
      <van-image-preview v-model="show" :images="images" @close="Close" @change="onChange">
        <template v-slot:index>第{{ index }}/{{images.length}}页</template>
      </van-image-preview>
    </template>
    <template v-else>
      <div class="videoplayer">
        <div class="bo-back">
          <van-icon name="arrow-left" @click="Close" />
        </div>
        <video ref="video" :src="VideoUrl" @click="isPlaying=!isPlaying"></video>
        <div class="play-btn" v-if="!isPlaying" @click="isPlaying=true">
          <van-icon name="play" />
        </div>
      </div>
    </template>
    <div class="information">
      <div class="author">
        <div class="author-icon">
          <img :src="AuthorIcon" alt />
        </div>
        <div class="author-name">{{AuthorName}}</div>
        <div class="ohter">
          <span>私信</span>
          <span>+关注</span>
        </div>
      </div>
      <div class="description">{{Description}}</div>
      <div class="tag">
        <span v-for="(item, index) in TagArr" :key="index">{{item.name}}</span>
      </div>
    </div>
  </div>
</template>

<script>
  import Vue from "vue";
  import {
    ImagePreview
  } from "vant";

  Vue.use(ImagePreview);
  export default {
    name: "CommintyCardDetail",
    data() {
      return {
        CommintyCardDetailData: {},
        show: true,
        index: 1,
        isPlaying: false
      };
    },
    watch: {

      isPlaying() {
        if (this.isPlaying) {
          this.$refs.video.play();
        } else {
          this.$refs.video.pause();
        }
      }
    },
    computed: {

      VideoUrl() {

        return this.CommintyCardDetailData.data.content.data.playUrl ? this.CommintyCardDetailData.data.content.data
          .playUrl : null;
      },
      TagArr() {
        return this.CommintyCardDetailData.data.content.data.tags ? this.CommintyCardDetailData.data.content.data.tags :
          null;
      },
      Description() {
        return this.CommintyCardDetailData.data.content.data.description ? this.CommintyCardDetailData.data.content.data
          .description : null;
      },
      AuthorName() {
        return this.CommintyCardDetailData.data.header.issuerName ? this.CommintyCardDetailData.data.header.issuerName :
          null;
      },
      AuthorIcon() {
        return this.CommintyCardDetailData.data.header.icon ? this.CommintyCardDetailData.data.header.icon : null;
      },
      images() {
        return this.CommintyCardDetailData.data.content.data.urls ? this.CommintyCardDetailData.data.content.data.urls :
          null;
      }
    },
    methods: {
      Close() {
        this.$router.go(-1);
        this.$store.commit("setShowBottomBar", true);
      },
      onChange(index) {
        this.index = index + 1;
      }
    },

    created() {
      this.CommintyCardDetailData = this.$store.state.CommintyCardDetailData;
    },
    // beforeRouteEnter(to, from, next) {
    //   // ...
    //   next((vm) => {
    //     vm.CommintyCardDetailData = vm.$store.state.CommintyCardDetailData;
    //   })
    // }
  };
</script>

<style lang="less" scoped>
  .CommintyCard-detail {
    background-color: #000;
    height: 100vh;

    .videoplayer {
      height: 100%;
      position: relative;

      .play-btn {
        width: 30px;
        height: 30px;
        text-align: center;
        position: absolute;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        margin: auto;

        i {
          color: #fff;
          font-size: 30px;
        }
      }

      .bo-back {
        padding: 5px;
        position: relative;
        top: 0;
        left: 0;
        z-index: 2;

        i {
          font-size: 20px;
          padding: 5px;
          color: #fff;
        }
      }

      video {
        width: 100%;
        display: block;
        position: absolute;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        margin: auto;
      }
    }

    .information {
      position: fixed;
      bottom: 0;
      left: 0;
      width: 100%;
      z-index: 2020;
      background-color: rgba(0, 0, 0, 0.8);
      padding: 20px;
      box-sizing: border-box;
      font-size: 14px;
      color: #fff;

      .author {
        display: flex;

        .author-name {
          flex: 1;
          margin-left: 5px;
          font-size: 14px;
          line-height: 30px;
        }

        .author-icon {
          width: 30px;
          height: 30px;
          border-radius: 50px;
          overflow: hidden;

          img {
            width: 100%;
            display: block;
          }
        }

        .ohter {
          line-height: 30px;

          span {
            margin-left: 5px;
            font-size: 12px;
            padding: 2px 8px;
            text-align: center;
            border-radius: 5px;

            &:first-child {
              background-color: rgb(78, 78, 245);
              border: 1px solid rgb(78, 78, 245);
            }

            &:last-child {
              border: 1px solid #fff;
            }
          }
        }
      }

      .description {
        font-size: 14px;
        color: #ddd;
        margin: 5px 0;
        display: -webkit-box;
        -webkit-line-clamp: 3;
        -webkit-box-orient: vertical;
        overflow: hidden;
      }

      .tag {
        span {
          padding: 0 2px;
          font-size: 12px;
          background-color: #444;
          margin-right: 5px;
        }
      }
    }
  }
</style>